    EQ: '=',
    GT: '>',
    GE: '>=',
    LT: '<',
    LE: '<=',
    NE: '!=',
    IN: i18n.t('one of'),
    '!IN': i18n.t('not one of'),
    LIKE: i18n.t('contains'),
    '!LIKE': i18n.t("doesn't contains")
